* {
  box-sizing: border-box;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #ccc;
}

.container{
  margin:10px;
}

.column1 {
  float: left;
  width: 15%;
  height: auto;
  padding: 10px;
}.btn{
  text-align: center;
  background-color: white;
  padding:0.5px 1px;
  border-radius: 5px;
}.btn h3{
  text-align: left;
  margin:0px 5px;
}.btn button{
  font-size: 15px;
  padding: 1px 10px;
  color: aliceblue;
  background-color: rgb(133, 189, 133);
  border:none;
  border-radius: 5px;
  margin:10px;
  opacity:0.8;
}.btn button:hover{
  cursor:pointer;
  background-color: rgb(31, 211, 14);
}.btn button:focus{
  background-color: rgb(31, 211, 14);
}


.column2 {
  float: left;
  width: 85%;
  height:100%;
}.mission-card{
  display:flex;
  flex-wrap:wrap;
  padding:10px;
  margin:-10px 20px 20px 40px;
}.card{
  background: white;
  border-radius:6px;
  box-shadow: 0px 5px 40px rgb(128, 127, 128);
  width:20%;
  height:auto;
  margin: 10px;
}.imgcard{
  text-align: center;
  width:100%;
  height: 30%;
}.imgcard img{
  width:150px;
  margin:5px;
  padding:5px;
  background-color: rgb(187, 184, 184);
  border-radius: 20%;
}.det-card{
  margin-top:20%;
  padding:10px;
  font-size: 15px;
  text-align: left;
}.style{
  text-align: center;
  font-size: 15px;
  font-weight: bold;
  color:blue;
}

section::after {
  content: "";
  display: table;
  clear: both;
}

footer{
  position: fixed;
  font-weight: bold;
  margin:5px;
  left: 0;
  bottom: 0;
  background-color: rgb(140, 140, 140);
  border-radius: 8px;
  padding: 5px;
  opacity: 0.6;
}



@media (max-width: 700px) {
  .column1 {
    width: 100%;
    height: auto;
    padding:10px 90px;
  }
  .column2{
    width: 100%;
    height: auto;
  }
  .card{
    text-align: left;
    width: 70%;
    margin-left:auto;
    margin-right:auto;
  }
}
@media (min-width:768px) {
  .column1 {
    width: 40%;
    height: auto;
    padding:10px 70px;
  }
  .column2{
    width: 60%;
    height: auto;
  }
  .card{
    text-align: left;
    width: 60%;

  }
}
@media (min-width:1024px) {
  .column1 {
    width: 40%;
    height: auto;
    padding:10px 95px;
  }
  .column2{
    width: 60%;
    height: auto;
  }
  .card{
    text-align: left;
    width: 40%;

  }
}

@media (min-width:1240px) {
  .column1 {
    width: 15%;
    height: auto;
    padding:10px 10px;
  }
  .column2{
    width: 85%;
    height: auto;
  }
  .card{
    text-align: left;
    width: 20%;

  }
}